<!-- 选手暂时 -->
<template>
  <div class="PlayerRankingHome">
    <div class="PlayerRanking_header">
      <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
      <span>节目top榜</span>
      <span class="playerList" @click="toMore">节目列表</span>
    </div>
    <div class="hideDiv"></div>
    <div class="SpecialTopicBody_bar">
      <ul>
          <li @click="SpecialBarBtn(index,item.id)" :class="SpecialBarindex==index?'Special_barColor':''" 
              v-for="(item,index) in SpecialTopicBodyBar" :key="index">
          {{item.names}}
        </li>
      </ul>
    </div>
    <div class="top_list_table">
        <div class="top_list_table_list">
            <span class="paiming">排名</span>
            <span class="xingming">节目名称</span>
            <span class="bianhao">参赛编号</span>
            <span class="piaoshu">票数</span>
            <span class="jiapiao">为Ta投票</span>
        </div>
        <ul class="TopList">
            <template v-for="(item,index) in RankingData" v-if="true">
            <li @click="toPlayerDetail(item.id)"  :key="index">
                <div class="top_list_img">
                <img v-if="(index+1) <= 3" :src="staticImgH+'paiming'+(index+1)+'.png'" alt="">
                <span v-if="(index+1) >= 4">{{index+1}}</span>
                </div>
                <span class="angelNameTwo ">{{item.username}}</span>
                <span class="angelPriceid">{{item.id}}</span>
                <span class="angelPriceTwo">{{item.votes}}</span>
                <div class="top_list_ta_vote" :class="{
                top_list_ta_vote1: (index+1) > step1 && (index+1) <= step2,
                top_list_ta_vote2: (index+1) > step2}">投票</div>
            </li>
            <li v-if="(index+1)===step2" class="list_tishi">
                <div class="tishi">最终前100名进入赛区决赛（音乐节），加油冲刺！</div>
            </li>
            </template>
        </ul>
        </div>
    <div class="gengduo">
      <div class="gengduo_right" v-if="this.currentPage >= this.lastPage">
        <span>没有更多了</span>
      </div>
      <!--加载的效果-->
      <div v-if="loading" class="gengduo_left">
        <div class="loading">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
        </div>
      </div>
    </div>
    <!-- 提示盒子 -->
    <transition name="fade">
      <div class="promptFather" v-if="showPrompt">
        <div class="prompt">
          {{promptContent}}
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import qs from 'qs'
export default {
    name:'PlayerRanking',
    data () {
        return {
            step1:3,
            step2:100,
            SpecialTopicBodyBar:'',
            SpecialBarindex:0,
            RankingData:[],
            RankingImgData:[],
            barId:0,
         
             // 提示盒子
            promptContent:'', //提示盒子的内容
            showPrompt:false,//提示盒子的吸收和显示

            loading: false,
            currentPage: 0,
            lastPage: 1,
        };
    },
//   components: {},
    computed:{
            ...mapState(['staticImgH','tokenH'])
        },
    mounted(){
        // 添加滚动事件，检测滚动到页面底部
        window.addEventListener('scroll', this.scrollBottom)
        this.$http.post('/yuanxiao/entering/division').then((res)=>{
            this.SpecialTopicBodyBar=res.data.data
            this.barId=res.data.data[0].id
            this.getlistData()
        })
    },

  methods: {
    // 节目列表
      toMore(){
        this.SearchConditions('')
        this.barIdINdexs('')
        this.$router.push('/ProgramListRanking')
    },
    //   跳节目详情
    toPlayerDetail(id){
        this.playerIds(id)//保存选手id
        this.addressIdIsSels('false') //投票盒子不显示 
        this.ShowDetailsPages('/TopListProgram')  //选手详情返回页面
        this.playDetailVoteDivs('true') //选手详情的投票盒子的消失
        this.$router.push('/ShowDetails')
    },
    // 分类
      SpecialBarBtn(index,id){
           this.RankingData=[]
          this.currentPage=0
            this.lastPage=1
          this.barId=id
          this.SpecialBarindex=index
          this.getlistData()
      },
      toReturn(){
          this.$router.push('/PlayerRanking')
      },
    //   获取数据
      getlistData(){
            if (this.currentPage > this.lastPage) {
                return 
            }
            this.loading = true
            this.currentPage += 1
            var obj=qs.stringify({
                page:this.currentPage
            })
            this.$http.post('/yuanxiao/entering/index/'+this.barId,obj).then((res)=>{
                this.loading = false
                if(res.data.code===200){
                     this.RankingData=this.RankingData.concat(res.data.data.data)
                     this.lastPage= res.data.data.last_page
                }else{
                     var self=this
                    clearInterval(self.timer2);
                            this.promptContent=res.data.msg
                            this.showPrompt=true
                            self.timer2=setTimeout(function(){
                                self.showPrompt=false
                                clearInterval(self.timer2);
                            },2000)
                        return false;
                }
               
            })
      },
      // 滚动到页面底部时，
            scrollBottom() {
                if (this.loading) {
                return false;
                }
                this.getlistData();
            },
      ...mapMutations(['playerIds','ShowDetailsPages','addressIdIsSels','playDetailVoteDivs','SearchConditions','barIdINdexs']),
  }
}

</script>
<style scoped lang='stylus'>
.PlayerRankingHome{
    width:100%;
    background :#fff;
}
.PlayerRanking_header{
    width:100%
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :center;
    position :fixed;
    top:0;
    left:0;
    background :#fff;
    z-index:10;
    >img{
        width:0.32rem;
        height:0.56rem;
        position :absolute;
        top:50%;
        margin-top:-0.28rem;
        left:0.27rem;
    }
    >span{
        font-size:0.48rem;
        color:rgba(0, 0, 0, 1);
    }
    >.playerList{
        height:0.4rem;
        line-height :0.4rem;
        font-size:0.4rem;
        color:rgba(0, 0, 0, 1);
        position :absolute;
        top:50%;
        margin-top:-0.2rem;
        right:0.27rem;
    }
}
.hideDiv{
  height:2.377rem;
}
.SpecialTopicBody_bar{
    width:100%;
    height:1.147rem;
    // border-bottom:0.03rem solid #eeeeee;
    padding:0 0.27rem;
	margin-bottom:0.37rem;
    background :#fff;
    position :fixed;
    top:1.23rem;
    left:0;
    z-index:5;
    >ul{
        height:1.147rem;
        display :flex;
        align-items :center;
         overflow-x:scroll;
        display: -webkit-box;
        -webkit-box-align: center;
        align-items :center;
        z-index :-1;
        >li{
            height:1.147rem;
            line-height :1.147rem;
            font-size:0.44rem;
            color:rgba(0, 0, 0, 0.6);
            margin-right:0.764rem;
        }
        >.Special_barColor{
            color:#FF9DAC;
            position :relative;
            &:before{
                 content:'';
                 width:100%;
                 height:0.04rem;
                 background :#FF9DAC;
                 position :absolute;
                 bottom:0.04rem;
                 left:0;
             }
        }
    }
}

.TopList{
    padding:0 0.4rem;
    margin-top:0.27rem;
    >li{
        width:100%;
        display:flex; 
        justify-content:space-between;
        align-items :center;
        margin-top:0.27rem; 
        margin-bottom:0.14rem;
		>span{
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
            -ms-flex-direction: column;
            flex-direction: column;
            -webkit-box-pack: justify;
            -ms-flex-pack: justify;
            justify-content: space-between;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
        }
       >.angelNameTwo{
           width:26%;
           font-size:0.347rem;
           color:#fff;
           font-weight:550;
		   display:flex;
           margin-top:0.07rem;
           margin-bottom:0.053rem;
           line-height :0.5rem;
           display:-webkit-box;
           -webkit-box-orient:vertical;
           word-break:break-all;
           overflow:hidden;
           -webkit-line-clamp:1;
           
       }
       >.angelPriceTwo{
           color:#fff;
		   display:flex;
           font-size:0.32rem;
           line-height :0.5rem;
		   margin-top:0.07rem;
           margin-bottom:0.053rem;
		   width:10%;
       }
	   >.angelPriceid{
           color:#fff;
		   display:flex;
           font-size:0.32rem;
           line-height :0.5rem;
		   margin-top:0.07rem;
           margin-bottom:0.053rem;
		   width:17%;
       }
        
    }
}
// 提示盒子
.promptFather{
    width:100%;
    position :fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto;
    display :flex;
    justify-content :center;
    align-items :center;
    z-index:999;
    >.prompt{
        padding:0.2rem 0.35rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.37rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
.top_list_table{ background:url(../../../static/mock/img/topbangbg.png) right center no-repeat; width:100%; background-size:cover; height:100%; position :absolute; padding-bottom:0.4rem; overflow-y: scroll;}
.top_list_table_list{ color:#fff; font-size:0.37rem; display:-webkit-box; display:-ms-flexbox; display:flex; -webkit-box-pack:justify; -ms-flex-pack:justify; justify-content:space-between; -ms-flex-wrap:wrap; flex-wrap:wrap; padding:0 0.4rem; margin-top:0.27rem;}
.top_list_table_list span{display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    margin-top: 0.2rem;}
.top_list_img{ color:#fff; width:10%; text-align:center; font-size: 0.34rem;}
.top_list_img img{ width:0.4rem; height:0.4rem; display:block; margin-left:25%;}
.top_list_ta_vote{ font-size:0.37rem; width:1.54rem; height:0.6rem; line-height:0.6rem; color:#fff; font-size:0.4rem; text-align:center; background:#FF9DAC; border-radius:2rem;}
.top_list_ta_vote1{ background:#4AABF9;}
.top_list_ta_vote2{ background:#5CD8C0;}
.paiming{ width:10%;}
.touxiang{ width:10%;}
.xingming{ width:26%;}
.bianhao{ width:17%;}
.piaoshu{ width:10%;}
.jiapiao{ width:17%;}
.tishi{ font-size: 0.34rem; color:#EC0A42; text-align:center; width:100%; margin-bottom:0.06rem; margin-top:0.44rem;}

.gengduo{ width:100%; height:2rem; margin-top:0.4rem; font-size:0.4rem;
>.gengduo_left{ margin-left:4.28rem; text-align:center; margin-top:1rem;}
>.gengduo_right{ width:100%; text-align:center;}
}
</style>



