<!-- 选手暂时 -->
<template>
  <div class="PlayerRankingHome">
    <div class="PlayerRanking_header">
        <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
        <span>我的关注</span>
        <div class="top_list"></div>
    </div>
    <div class="hideDiv"></div>
        <ul class="HomeAngel_listTwo">
            <li v-for="(item, seqNum) in topThree" :key='seqNum' @click="toPlayerDetail(item.player_id, 'false')">
                <div class="HomeAngel_listTwotop">
                    <img class="Two_img"
                    :src="item.head_pic"  alt="">
                </div>
                <span class="angelNameTwo">选手：{{item.username}}</span>
                <span class="angelPriceTwo">编号：{{item.player_id}}</span>
            </li>
        </ul>
        <!-- 提示盒子 -->
        <transition name="fade">
        <div class="promptFather" v-if="showPrompt">
            <div class="prompt">
            {{promptContent}}
            </div>
        </div>
        </transition>   
     </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import qs from 'qs'
export default {
    name:'PlayerRanking',
    data () {
        return {
            topThree: [],

             // 提示盒子
            promptContent:'', //提示盒子的内容
            showPrompt:false,//提示盒子的吸收和显示
            timer2:'',
        };
    },
//   components: {},
        computed:{
            ...mapState(['staticImgH'])
        },

        mounted(){
                this.getlistData()
        },
        methods: {
            //   跳选手详情
            toPlayerDetail(id, voteFlag){
                this.playerIds(id)//保存选手id
                this.addressIdIsSels('false') //进入选手详情默认不选择地址
                this.PlayerDetailPages('/MineAttention')  //选手详情返回页面
                this.playDetailVoteDivs(voteFlag) //选手详情的投票盒子的消失
                this.$router.push('/PlayerDetails')
            },
            getlistData(){
                var obj=qs.stringify({
                    page:1
                })
                this.$http.post('yuanxiao/user/user_atten',obj).then((res)=>{
                    if(res.data.code===200){
                        this.topThree=res.data.data
                    }else{
                        this.alert(res.data.msg)
                    }
                });
            },
            toReturn(){
                this.$router.push('/Mine')
            },
            alert(msg){
                let self=this;
                clearInterval(self.timer2);
                this.promptContent=msg
                this.showPrompt=true
                self.timer2=setTimeout(function(){
                    self.showPrompt=false
                    clearInterval(self.timer2);
                },2000)
            },
            ...mapMutations(['playerIds','PlayerDetailPages','addressIdIsSels','playDetailVoteDivs']),
        }
}

</script>
<style scoped lang='stylus'>
.PlayerRankingHome{
    width:100%;
    background :#fff;
}
.PlayerRanking_header{
    width:100%
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :center;
    position :fixed;
    top:0;
    left:0;
    z-index:5;
    background :#fff;
    >img{
        width:0.32rem;
        height:0.56rem;
        position :absolute;
        top:50%;
        margin-top:-0.28rem;
        left:0.27rem;
    }
    >span{
        font-size:0.48rem;
        color:rgba(0, 0, 0, 1);
    }
	>.top_list{right:0.27rem; position: absolute; font-size:0.44rem;}
}
.hideDiv{
  height:1.23rem;
}
.SpecialTopicBody_bar{
    width:100%;
    height:1.147rem;
    // border-bottom:0.03rem solid #eeeeee;
    padding:0 0.27rem;
    background :#fff;
    >ul{
        height:1.147rem;
        display :flex;
        align-items :center;
         overflow-x:scroll;
        display: -webkit-box;
        -webkit-box-align: center;
        align-items :center;
        z-index :-1;
        >li{
            height:1.147rem;
            line-height :1.147rem;
            font-size:0.37rem;
            color:rgba(0, 0, 0, 0.6);
            margin-right:0.764rem;
        }
        >.Special_barColor{
            color:rgba(0, 0, 0, 0.8);
            font-weight:550;
            position :relative;
            // &:before{
            //     content:'';
            //     width:100%;
            //     height:0.04rem;
            //     background :#f15055;
            //     position :absolute;
            //     bottom:0.03rem;
            //     left:0;
            // }
        }
    }
}

.HomeAngel_listTwo{
    display :flex;
    flex-wrap:wrap;
    margin-top:0.27rem;
    padding:0 0.4rem;
    >li{
        width:2.6rem;
        display:flex;
        flex-direction :column;
		position: relative;
        margin-right:0.46rem;
        border-radius:0.16rem;
        box-shadow:0px 3px 12px 0px rgba(255,204,212,0.5);
        margin-bottom:0.32rem;
        padding-bottom:0.27rem;
        &:last-child{
            margin-right:0;
        }
        .HomeAngel_listTwotop{
            width:2.6rem;
            height:2.6rem;
            >img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        
       >.angelNameTwo{
           font-size:0.347rem;
           color:rgba(0, 0, 0, 0.8);
           font-weight:550;
           margin-top:0.27rem;
           margin-bottom:0.053rem;
           line-height :0.5rem;
           display:-webkit-box;
           -webkit-box-orient:vertical;
           word-break:break-all;
           overflow:hidden;
           -webkit-line-clamp:1;
            padding:0 0.1rem;
       }
       >.angelPriceTwo{
           color:rgba(0, 0, 0, 0.8);
           font-size:0.32rem;
           line-height :0.45rem;
            padding:0 0.1rem;
       }

    }
}
// 提示盒子
.promptFather{
    width:100%;
    position :fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto;
    display :flex;
    justify-content :center;
    align-items :center;
    z-index:999;
    >.prompt{
        padding:0.2rem 0.35rem;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.37rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
.SpecialTopicBody_content{padding:0 0.4rem;}
.SpecialTopicBody_center{padding:0.4rem 0.4rem; margin-bottom:0.2rem; box-shadow:0px 3px 12px 0px rgba(255,204,212,0.5); border-radius:0.2rem;}
.SpecialTopicBody_li{justify-content: space-between; display: flex; margin-bottom:0.4rem;}
.SpecialTopicBody_li span{ font-size: 0.4rem;}
.SpecialTopicBody_li .more{ color:#999; font-size: 0.37rem;}
.Two_img{width:100%; height:100%; border-radius:0.16rem;}
.top_img{ position:absolute; color:#fff; margin-top:0.1rem; margin-left:0.1rem; width:0.4rem; height:0.4rem;}
.top_img img{ width:0.4rem; height:0.4rem; display:block;}
.ta_vote{ position:absolute; font-size:0.37rem; margin-left:0.46rem; right:0; bottom:1.21rem; width:2rem; height:0.8rem; line-height:0.9rem; color:#fff; font-size:0.4rem; text-align:center; background:url(../../../static/mock/img/toupiao.png) right center no-repeat; background-size:cover;}
.ta_vote1{ background:url(../../../static/mock/img/toupiao1.png) right center no-repeat; background-size:cover;}
</style>



