<!--  -->
<template>
  <div class="ShowDetails">
        <div class="ShowDetails_header">
             <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" >
            <span>节目详情</span>
            <!-- <span class="moreVideo" @click="toPlayDetailstyle">更多视频</span> -->
        </div>
        <div class="hideDiv"></div>
        <div @click.stop class="small_Carousel" v-if="smallSwiper.length>=1">
                <swiper v-if="smallSwiper.length>=1" class="small_swiperWrap" :options="swiperOption">
                    <swiper-slide class="swiperSlide" v-for="(item,index) in smallSwiper" :key="index">感谢「 <img :src="item.head_pic"><span>{{item.nickname}}</span>」为「 {{showDetailData.username}}」助力投票{{item.amount}}票</swiper-slide>
                </swiper>
        </div>
        <div class="videoBox" >
            <div @click="toplayVideo(showDetailData.videos)" class="mask">
                <img class="playBtn" :src="staticImgH+'bofang.png'"/>
            </div>
            <video width="100%" height="100%"
                :poster="showDetailData.videos+'?x-oss-process=video/snapshot,t_10000,m_fast'">
            </video>
        </div>
        <div class="show">
            <div class="show_title">{{showDetailData.username}}</div>
            <div class="show_text" v-html="showDetailData.content"></div>
        </div>
        <ul class="show_bottom">
            <li>
                <span class="show_bottom_tick">{{showDetailData.votes}}</span>
                <span>总票数</span>
            </li>
            <li>
                <span class="show_bottom_tick">{{showDetailData.division_ranking}}</span>
                <span>赛区排名</span>
            </li>
        </ul>
        <div class="show_btn">
            <span class="Dvote" @click="vote">点击投票</span>
            <span class="Fshare" @click.stop="shareC">为Ta分享</span>
            <!-- <span class="Svideo" @click="toPlayDetailstyle">节目视频</span> -->
        </div>
        <div class="show_btn_hide"></div>
        <!-- 提示分享 -->
            <div class="shareText_wrap" v-if="shareTextShow"  @click="hideShare">
                <img :src="staticImgH+'timg.jpg'"/>
                <div class="shareText">
                    点击右上角，为ta分享助力投票
                </div> 
            </div>
        
        <!-- 投票盒子 -->
            <div class="vote_wrap" v-if="voteShow" @click.stop="hideVote">
                <div class="vote"  @click.stop>
                    <!-- 免费票 -->
                    <div class="FreeTicket">
                        <span class="FreeTicket_title">每日免费投票 （每人每天可投两票  <span> 剩余{{personData.user_votes}}票 </span>）</span>
                        <span class="FreeTicket_btn" @click.stop="freeticket">立即投票</span>
                    </div>
                    <!-- 助力票 -->
                    <div class="AssistTicket">
                            <!-- <div class="AssistTicket_title"> <span class="AssistTicket_title_first">卡包助力投票</span><span>(分享卡包，成功购买可获得卡包金额10倍的票数)</span> </div>   -->
                            <div class="AssistTicket_title"> <span class="AssistTicket_title_first">卡包助力投票</span></div>  
                            <span class="AssistTicket_text">助力票为实物，投票后请输入正确的收货地址，我司正常发货。</span>
                            <div class="SpecialTopicBody_bar">
                                <!-- 商品列表 -->
                                <ul>
                                    <li  :class="barcolorIndex==index?'SpecialTopicBody_barColor':''" @click.stop="getShopDes(item.goods_id,index,item.vote)" v-for="(item,index) in shopList" v-if=" Number(index+1)" :key="index">
                                        <img :src="item.original_img" alt="">
                                        <span @click.stop="toshopdetail(item.goods_id,item.vote)">查看详情>></span>
                                    </li>
                                </ul>
                            </div>
                            <div class="AssistTicket_btn" @click.stop="AssistTicket">立即投票</div>
                    </div>
                </div>
            </div>
        
        <!-- 投免费票输入框 -->
            <div class="EnterTickets" @click="hideEnterTickets" v-if="EnterTicketsShow">
                <div class="EnterTickets_input" @click.stop>
                    <div class="EnterTickets_inputA">
                        <input type="text" v-model="freeTicNum" @input = "inputing"  placeholder="请输入您要投的票数"/>
                        <span @click.stop="allticNumC">全部</span>  
                    </div> 
                    <div class="EnterTickets_inputB">免费票:<span>{{personData.user_votes}}</span>票</div>
                 </div>
                 <div class="EnterTickets_btn" @click.stop="freeInput">
                    立即投票
                </div>
            </div>
        <!-- 提示盒子222222222222222222222222 -->
            <transition name="fade">
                <div class="promptFather" v-if="showPrompt">
                    <div class="prompt" >
                        {{promptContent}}
                    </div>
                </div>
            </transition>
        <!-- 订单提交后提示 -->
            <div class="orderTIshi_wrap" @click="sureOrderTi" v-if="orderTIshiSHow">
                <div class="orderTIshi" @click.stop>
                    <span class="orderTIshi_des">您已成功为{{showDetailData.username}}投票{{shopVotes}}票，将按照所填快递地址寄出精美礼品</span>
                    <span class="orderTIshi_btn" @click="sureOrderTi">确定</span>
                </div>
            </div>

            <!-- 扫码关注 -->
            <attention/>  
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import wx from 'weixin-jsapi'
import qs from 'qs'
import attention from './../attention/attention'
export default {
    data () {
        return {
            // 小轮播
            swiperOption: {//swiper3
                direction: 'vertical',
                observer:true,
                observeParents:true,
                loop:true,
                autoplay: {
                　　    disableOnInteraction: false,
                　　},
                speed:300,
            },
            smallSwiper:'',//顶部小轮播数据
            showDetailData:'',//节目详情接口
            shareTextShow:false,  //分享提示

            voteShow:false,
            barcolorIndex:-1,
            freeTicNum:1,
            goodId:'',
            EnterTicketsShow:false,
            shopList:'',
            playerInterest:'',
            orderSn:'',
            dataResult:'',
            personData:'',
            orderTIshiSHow:false,

            // 提示盒子
            promptContent:'', //提示盒子的内容
            showPrompt:false,//提示盒子的吸收和显示
            timer2:'',
            shopVotes:'',
        };
    },
    
    computed:{
        ...mapState(['apiH','staticImgH','playerId','ShowDetailsPage','abortTime','addressIdIsSel','addressId','Shopvote','playDetailVoteDiv','shopDetatilshow','barcolorIndexShop','playDetailShopDES'])
    },
    components: {
      attention
    },
    mounted(){
        //  判断是否是分享出去的
            var shopUrl = window.location.href
            //截取？后的商品id
            var shopCan, value;  //初始化 
            shopCan = shopUrl.indexOf("#")  //找到？的下标
            shopUrl=shopUrl.substr(shopCan+1)
            shopCan=shopUrl.indexOf('?')
            if(shopCan > 0){
                shopUrl = shopUrl.substr(shopCan + 1)  //截取？后面的内容
                var shopArr = shopUrl.split('&') //分割成数组 
                var shopUrlId = {};// 初始化对象 找到的goodId放到里面
                for(var i = 0; i < shopArr.length; i++) {//循环shopArr数组
                    shopCan = shopArr[i].indexOf("=");  //找到=号的下标
                    if(shopCan > 0){ //判断有没有=
                        value = shopArr[i].substring(shopCan + 1); //找到=后面的值并截取     =>value
                        shopCan = shopArr[i].substring(0, shopCan);//找到=前面的值  =》key
                        shopUrlId[shopCan] = value;  // key value放到shopUrlId对象里
                    }
                }
                if(shopUrlId.player_id){
                    this.playerInterest=shopUrlId.player_id
                }
            }else{
                this.playerInterest=this.playerId
            }

                    //   获取免费票数 
                        this.getpaioNUm()
                    //  获取小轮播数据
                        this.getSmallSwiperData()
                    //  分享
                        this.toshare()
                    // 助力商品列表
                        this.HelpShopList()
                    // 节目详情数据
                        this.getProgramData()
    },
    methods: {
        // 隐藏提交订单提示盒子显示
                   sureOrderTi(){
                       if(this.ShowDetailsPage){
                            this.orderTIshiSHow=false
                        }else{
                            this.$router.push('/')
                        }
                   },
         inputing(){
            var val=this.freeTicNum.replace(/[^\d]/g,'')
            this.freeTicNum=val
            if(this.freeTicNum>this.personData.user_votes){
                    this.freeTicNum=this.personData.user_votes
            }  
        },
        //助力商品列表
        HelpShopList(){
            this.$http.post('/yuanxiao/goods/goods_list').then((res)=>{
                    if(res.data.code==200){
                        this.shopList=res.data.data.result
                        if(this.Shopvote){
                            this.shopVotes=this.Shopvote
                        }
                    }else{
                        this.toastMsg(res.data.msg)
                    }
            })
        },
        // 获取小轮播数据
       getSmallSwiperData(){
            var obj=qs.stringify({
                player_id:this.playerInterest
            })
            this.$http.post('/yuanxiao/entering/vote_list',obj).then((res)=>{
                    if(res.data.code==200){
                        this.smallSwiper=res.data.data
                    }else {
                        this.toastMsg(res.data.msg)
                    }
            })
        },
        // 节目详情数据
        getProgramData(){
             var obj=qs.stringify({
                player_id:this.playerInterest
            })
            this.$http.post('/yuanxiao/entering/info',obj).then((res)=>{
                if(res.data.code==200){
                    this.showDetailData=res.data.data
                    //判断是否显示助力盒子
                        if(this.playDetailVoteDiv=='true'){   //  playDetailVoteDiv 是判断投票盒子是否显示
                            this.voteShow=true    //显示助力盒子
                            if(this.addressIdIsSel=='true'){   //addressIdIsSel   是判断地址是否选中 或者 没有地址是否添加上地址了 ture为有地址可以走支付
                                this.goodId=this.playDetailShopDES.goodsId   
                                this.playerInterest=this.playDetailShopDES.playerId
                                this.barcolorIndex=this.playDetailShopDES.barcolorIndex   //被选中商品下标
                                this.AssistTicketTwo()
                            }else if(this.shopDetatilshow=='true'){ //单纯的选中商品
                                this.goodId=this.shopgoodId
                                this.barcolorIndex=this.barcolorIndexShop
                            }
                        }else if(this.playDetailVoteDiv=='false'){
                            this.voteShow=false  //隐藏助力盒子
                        }
                }else {
                    this.toastMsg(res.data.msg)
                }
            })
        },
         // 获取免费票数  
            getpaioNUm(){
                    this.$http.post('/yuanxiao/user/info').then((res)=>{
                        if(res.data.code==200){
                            this.personData=res.data.data
                        }else{
                            this.toastMsg(res.data.msg)
                        }
                    })
            },
        //   隐藏投票盒子
            hideVote(){
                this.voteShow=false
                this.barcolorIndex=-1
            },
        
        // 获取goodid
            getShopDes(goodId,index,vote){
                this.barcolorIndex=index  //被选中下标
                this.goodId=goodId   
                this.shopVotes=vote
                this.Shopvotes(vote)//为了返回到当前页面的时候 保存商品的票数
            },
        //   到商品id
            toshopdetail(goodid,vote){
                this.addressIdIsSels('false') //商品页默认地址不选中
                this.shopDetatilshows('true')  // 判断是不是商品详情页返回到此页面
                this.playDetailVoteDivs('true')  //设置为true是为了返回到当前页面的时候 助力盒子显示
                this.barcolorIndexShops(this.barcolorIndex) //为了返回到当前页面的时候 显示哪个商品被选中状态
                this.Shopvotes(vote)//为了返回到当前页面的时候 保存商品的票数
                this.shopDetailReturns('/ShowDetails')//商品页返回哪
                this.shopgoodIds(goodid)  //给商品页传gooid
                this.playerIds(this.playerInterest)  //保存当前选手的playerID
                this.userIdHInterests(this.playerInterest)  //给商品页传获得利益的选手编号
                this.$router.push('/shopDetail')  
            },
            // 助力票 （立即投票按钮）
            AssistTicket(){
                 var timestamp=new Date().getTime()/1000;
                if(Number(timestamp)>Number(this.abortTime)){
                        this.toastMsg('投票已过期')
                }else{
                        // 先判断有没有goodId
                            if(this.goodId==''){
                                this.toastMsg('请先选择商品')
                            }else{ //否则判断有没有地址
                                    if(this.addressIdIsSel=='false'){   //如果 addressIdIsSel为false 选择获取添加地址
                                        this.$http.post("/yuanxiao/user/address_list").then((res)=>{
                                            this.playDetailVoteDivs('true')  //设置为true是为了返回到当前页面的时候 助力盒子显示
                                            this.playDetailShopDESs({goodsId:this.goodId,playerId:this.playerInterest,barcolorIndex:this.barcolorIndex})  //保存信息，为返回到当前页面的时候可以进行支付
                                            if(res.data.data.length){  //跳选择地址列表页面
                                                this.ReceiptAddressPages('/ShowDetails')
                                                this.$router.push('/ReceiptAddress')
                                            }else{  //跳添加地址列表页面
                                                this.ReceiptAddressAddPages('/ShowDetails')
                                                this.$router.push('/ReceiptAddressAdd')
                                            }
                                        })
                                    }else if(this.addressIdIsSel=='true'){  //否则进行支付
                                            this.AssistTicketTwo()
                                    }
                            }
                }
                
            },
        // 助力
            AssistTicketTwo(){
                var obj=qs.stringify({
                    goods_id:this.goodId,
                    player_id:this.playerInterest,
                    address_id:this.addressId,
                    prom_type:1,
                })
                this.$http.post('/yuanxiao/goods/goods_buyer',obj).then((res)=>{
                    if(res.data.code==200){
                        this.orderSn=res.data.data.result  //订单编号
                        this.getPayPermit()
                    }else{
                        this.toastMsg(res.data.msg)
                    }
                })
            },
        // 支付 wx.config
            getPayPermit(){
                var PayPermitObj=qs.stringify({
                    order_sn:this.orderSn,
                    trade_type:'JSAPI',
                })
                this.$http.post('/yuanxiao/wechat/dopay',PayPermitObj).then((res)=>{
                        if(res.data.code==200){
                            var data=res.data.data
                            this.dataResult=data //微信支付认证信息
                            var vm=this
                            wx.config({
                                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                                appId: data.appId, // 必填，公众号的唯一标识
                                timestamp:data.timeStamp, // 必填，生成签名的时间戳
                                nonceStr: data.nonceStr, // 必填，生成签名的随机串
                                signature: data.sign,// 必填，签名
                                jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表
                            });
                            this.payLe()
                        }else{
                             this.toastMsg(res.data.msg)
                        }
                })
            },
        //支付  wx.chooseWXPay
            payLe(){
                    this.addressIdIsSels('false') //商品页默认地址不选中
                    var vm=this
                    wx.ready(function(){
                        wx.chooseWXPay({
                            timestamp: vm.dataResult.timeStamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                            nonceStr: vm.dataResult.nonceStr, // 支付签名随机串，不长于 32 位
                            package: vm.dataResult.packag, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=\*\*\*）
                            signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                            paySign:  vm.dataResult.sign, // 支付签名
                            success: function (res) {
                                    vm.addressIdIsSels('false') //商品页默认地址不选中
                                    vm.orderTIshiSHow=true
                                    vm.getpaioNUm()
                                    vm.getProgramData()
                                // this.myOrderListPages('/PlayerDetails')  //订单列表页返回
                                // vm.orderTypes('WAITSEND')  //订单页面orderType
                                // vm.orderNums(2)   //订单页面导航下标
                                // vm.$router.push('/orderList')  //支付成功后跳订单列表
                            },
                            fail(){
                                alert('支付失败')
                            }
                        });
                    }) 
            },
            // 
            hideEnterTickets(){
                this.EnterTicketsShow=false
            },
             //  全部免费票
            allticNumC(){
                this.freeTicNum=this.personData.user_votes
            },
            //   免费票（点击投票）
            freeticket(){
                 var timestamp=new Date().getTime()/1000;
                if(timestamp>this.abortTime){
                    this.toastMsg('投票已过期')
                }else{
                    if(this.personData.user_votes>=1){
                        this.EnterTicketsShow=true
                    }else{
                        this.toastMsg('您的免费票数为0')
                    }
                }
            },
             // 免费票输入框
           freeInput(){
               if(this.freeTicNum > this.personData.user_votes){
                   this.freeTicNum=1
                   this.toastMsg('您输入的票数超出了您现有的免费票数')
                   return
               }
               if(this.freeTicNum <=0){
                   this.freeTicNum=1
                   this.toastMsg('您输入的票数不能小于0')
                   return
               }
               var obj=qs.stringify({
                    player_id:this.playerInterest,
                    votes:this.freeTicNum
                })
                this.$http.post('/yuanxiao/entering/spend_vote',obj).then((res)=>{
                    this.EnterTicketsShow=false
                    this.freeTicNum=1
                    if(res.data.code==200){
                        this.getpaioNUm()
                        this.getProgramData()
                        this.toastMsg('投票成功')
                    }else{
                        this.toastMsg(res.data.msg)
                    }
                })
           },
         //   分享
    toshare(){
            var Wobj=qs.stringify({
                player_id:this.playerInterest,
                type:5,
            })
            this.$http.post('/yuanxiao/wechat/get_sign',Wobj).then((res)=>{
                if(res.data.code==200){
                    var data=res.data.data
                    this.test=data.test
                        wx.config({
                            debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                            appId: data.appId, // 必填，公众号的唯一标识
                            timestamp:data.timestamp, // 必填，生成签名的时间戳
                            nonceStr: data.nonceStr, // 必填，生成签名的随机串
                            signature: data.signature,// 必填，签名
                            jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // 必填，需要使用的JS接口列表
                        });
                        this.toFriend()
                        this.toFriendQuan()
                }else{
                    this.toastMsg(res.data.msg)
                }
        })
    },
    //   分享给朋友
            toFriend(){
                        var vm=this
                        var realLocation=vm.apiH+'/#/ShowDetails?player_id='+vm.playerInterest 
                        wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
                            wx.onMenuShareAppMessage({ 
                                title:'国学公开课--四海童乐闹元宵', // 分享标题
                                desc:'《四海童乐闹元宵》盛大启动，快来给心爱的节目投票吧！', // 分享描述
                                link:vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation), // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                                imgUrl: vm.apiH+'/static/mock/img/cusKefu.jpg', // 分享图标
                                success: function (res) {
                                }
                            })
                        });
            },
        //   分享到朋友圈
            toFriendQuan(){
                var vm=this
                var realLocation=vm.apiH+'/#/ShowDetails?player_id='+vm.playerInterest 
                wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
                        wx.onMenuShareTimeline({
                                title:'国学公开课--四海童乐闹元宵', // 分享标题
                                link: vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation),  // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                                imgUrl:vm.apiH+'/static/mock/img/cusKefu.jpg', // 分享图标
                                success: function (res) {
                                },
                        })
                });
            },
        //   分享提示显示
            shareC(){
                this.shareTextShow=true
            },
        //   隐藏提示分享
            hideShare(){
                this.shareTextShow=false
            },
        // 返回
            toReturn(){
                if(this.ShowDetailsPage){
                    this.$router.push(this.ShowDetailsPage)
                }else{
                    this.$router.push('/')
                }
            },
        // 更多视频
            toPlayDetailstyle(){
                this.playerIds(this.playerInterest)   //给选手详情风采页传playerId
                this.$router.push('/Programvideo')
            },
        // 投票
            vote(){
                this.addressIdIsSels('false')   // 每点击投票 重置为未选中地址状态
                this.shopDetatilshows('false')  //
                this.goodId=''  //每点击投票 商品id重置为空
                this.voteShow=true   //投票盒子显示
            }, 
        // 节目视频
            toplayVideo(itemValue){
                this.$router.push({path:'/videoDetail',query:{videoSrc:itemValue}})
            },
        // 弹框提示
            toastMsg(msg) {
                let self = this
                clearInterval(self.timer2);
                this.promptContent = msg
                this.showPrompt = true
                self.timer2 = setTimeout(function () {
                self.showPrompt = false
                clearInterval(self.timer2);
                }, 2000)
            },

