<!-- 选手视频页 -->
<template>
  <div class="PlayerStyle">

    <div class="PlayerRanking_header">
      <img @click="toReturn" :src="staticImgH+'zuojiantou.png'" alt="">
    </div>
    <div class="hideDiv"></div>
    <div class="showInfo">
                <span class="showInfo_userName">{{video_info.username}}</span>
                <span class="showInfo_ranking">节目排名：23名</span>
    </div>
    <div class="video">=
      <div class="contenter flex_center">
          <div class="videoBox" >
                <div  v-if="mobile==='android'"  @click="playOrPause()" class="play mask flex_center">
                    <img v-show="show" class="playBtn" :src="staticImgH+'bofang.png'"/>
                </div>
                <div v-if="mobile==='iPhone'"  @click="playOrPause()" class="play mask flex_center">
                    <img v-show="showIPhone" class="playBtn" :src="staticImgH+'bofang.png'"/>
                </div>
                <!-- x5-video-player-fullscreen="true"
                  x5-playsinline  x-webkit-airplay 
                        x5-video-player-type="h5"  
                        x5-video-player-fullscreen="false" preload="preload" android :class="{'videoOpcity':videoOpcityY}"-->
                    <video  id="video" ref="player"    v-if="mobile==='android'"  width="100%" height="100%"
                            x5-video-player-type="h5"
                            x5-video-player-fullscreen="true"
                            preload="auto"
                            x5-playsinline="true"
                            :poster="video_info.video+'?x-oss-process=video/snapshot,t_10000,m_fast'"
                            :src="video_info.video"
                            >
                    </video>
                    <video ref="player" autoplay="autoplay" v-if="mobile==='iPhone'" id="video"
                          width="100%"
                          height="100%"
                          preload="auto"
                          webkit-playsinline
                          playsinline="true"
                          :poster="video_info.video+'?x-oss-process=video/snapshot,t_10000,m_fast'" 
                          :src="video_info.video"  >
                    </video>
                <div id="output"></div>
          </div>
      </div>

    </div>
    <div class="scrollWrap" ref="scrollWrapWidth">
      <div :style="{width:scrollwidth+'px'}" class="scrollDiv">
      </div>
    </div>
    <div class="video_caozuo">
      <span class="video_toupiao" @click="toPlayerDetail(currentPlayerData.id)">投票</span>
    </div>
    <!-- 提示盒子 -->
    <transition name="fade">
      <div class="promptFather" v-if="showPrompt">
        <div class="prompt">
          {{promptContent}}
        </div>
      </div>
    </transition>
    <!-- 提示分享 -->
        <div class="shareText_wrap" v-if="shareTextShow"  @click="hideShare">
            <img :src="staticImgH+'timg.jpg'"/>
            <div class="shareText">
                点击右上角，为ta分享助力投票
            </div> 
        </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex'
  import {mapMutations} from 'vuex'
  import wx from 'weixin-jsapi'
  import qs from 'qs'

  export default {
    name: "PlayerStyleDetailed",
    data() {
      return {
         video:'',
        shareTextShow:false,//分享提示，默认隐藏
        followFlag: false,
        zanFlag: false,
        voteFlag: false,
        videoOpcityY:true,
        playerOptions:{},

        show:true,
        showIPhone:false,
        mobile:"",
        text:"",
        scrollWrapWidthNum:'',

        PlayerStyleData: '',
        currentPlayerData: {},
        scrollwidth:0,
        // 提示盒子
        promptContent: '', //提示盒子的内容
        showPrompt: false,//提示盒子的吸收和显示,
        video_info: {
          username:	null,
          player_id:	0,
          head_pic:	null,
          video_introduction:	null,
          names: '',
          division_id: null,
          concern: 0,
          is_videos: 0,
          rank: 0,
          test:'',//分享视频数据
          output:'',
          video:'',
        }
      };
    },

    computed: {
      ...mapState(['staticImgH', 'tokenH', 'playerStyleDetailedPlayer','playerVideoPage','apiH'])
    },
    created(){
      this.text=navigator.appVersion
      this.mobile = navigator.appVersion.indexOf('iPhone') !== -1 ? 'iPhone' :  'android'
    },
    mounted() {
      this.firstPanduan()
      this.video = document.getElementById('video')
      this.output = document.getElementById("output");
      this.video.currentTime = 0.1;
      console.log(this.currentPlayerData)
      //视频详情
      let obj = qs.stringify({
        v_id: this.currentPlayerData.id,
        player_id: this.currentPlayerData.player_id
      })
      this.$http.post('/yuanxiao/entering/vide_info', obj).then((res) => {
        if (res.data.code === 200) {
          this.video_info = res.data.data
          // this.video.play()
          this.WShare()
          this.scrollWrapWidthNum=this.$refs.scrollWrapWidth.offsetWidth;
          this.$refs.player.addEventListener('timeupdate', this._currentTime);
        }
      })
        
    },

    methods: {
      _currentTime(){
          if(this.$refs.player.currentTime==this.$refs.player.duration){
            this.show=true
            this.showIPhone=true
          }
          var scale=Math.min(1, this.$refs.player.currentTime / this.$refs.player.duration)
          this.scrollwidth=this.scrollWrapWidthNum * scale
      },
      // 
      // 点击了解更多
          toHome(){
            this.$router.push('/PlayerStyle')
          },
      // 分享提示
          toShare(){
              this.shareTextShow=true
          },
      //   隐藏提示分享
            hideShare(){
                this.shareTextShow=false
            },
      // 
      firstPanduan(){
                  //判断是否是分享出去的
                      var shopUrl = window.location.href
                        //var shopUrl = 'http://mobile.aibebi.cn/aibei/shopList.html?goods_id=1482'
                        //截取？后的商品id
                        var shopCan, value;  //初始化 
                        shopCan = shopUrl.indexOf("#")  //找到？的下标
                        shopUrl=shopUrl.substr(shopCan+1)
                        shopCan=shopUrl.indexOf('?')
                        if(shopCan > 0){
                            shopUrl = shopUrl.substr(shopCan + 1)  //截取？后面的内容
                            var shopArr = shopUrl.split('&') //分割成数组 
                            var shopUrlId = {};// 初始化对象 找到的goodId放到里面
                            for(var i = 0; i < shopArr.length; i++) {//循环shopArr数组
                                shopCan = shopArr[i].indexOf("=");  //找到=号的下标
                                if(shopCan > 0){ //判断有没有=
                                    value = shopArr[i].substring(shopCan + 1); //找到=后面的值并截取     =>value
                                    shopCan = shopArr[i].substring(0, shopCan);//找到=前面的值  =》key
                                    shopUrlId[shopCan] = value;  // key value放到shopUrlId对象里
                                }
                            }
                            if(shopUrlId.v_id && shopUrlId.id){
                                this.currentPlayerData=shopUrlId
                            }else{
                                this.currentPlayerData=this.playerStyleDetailedPlayer
                            }
                        }else{
                                this.currentPlayerData=this.playerStyleDetailedPlayer
                        }
            },
      // 微信分享
            WShare(){
                var Wobj=qs.stringify({
                    player_id:this.currentPlayerData.id,
                    type:3,
                })
                this.$http.post('/huaxia/wechat/get_sign',Wobj).then((res)=>{
                    if(res.data.code==200){
                        var data=res.data.data
                        this.test=data
                            wx.config({
                                debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                                appId: data.appId, // 必填，公众号的唯一标识
                                timestamp:data.timestamp, // 必填，生成签名的时间戳
                                nonceStr: data.nonceStr, // 必填，生成签名的随机串
                                signature: data.signature,// 必填，签名
                                jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // 必填，需要使用的JS接口列表
                            });
                            this.toFriend()
                            this.toFriendQuan()
                    }else{
                        this.toastMsg(res.data.msg);
                    }
                })
            },
        //   分享给朋友
            toFriend(){
                var vm=this
                var realLocation=vm.apiH+'/#/PlayerStyleDetailed?v_id='+vm.currentPlayerData.v_id+'&id='+vm.currentPlayerData.id
                wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
                    wx.onMenuShareAppMessage({ 
                        title:vm.test.test, // 分享标题
                        desc:'选手视频', // 分享描述
                        link:vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation), // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                        imgUrl: vm.video_info.username, // 分享图标
                        success: function (res) {
                                   var GetVotesData=qs.stringify({
                                        type:3,
                                        amount:1
                                    })
                                    vm.$http.post('huaxia/user/get_votes',GetVotesData).then((resTwo)=>{
                                        if(res.data.code!==200){
                                            alert('yes')
                                            clearInterval(vm.timer2);
                                            vm.promptContent=resTwo.data.msg
                                            vm.showPrompt=true
                                            vm.timer2=setTimeout(function(){
                                                vm.showPrompt=false
                                                clearInterval(vm.timer2);
                                            },2000)
                                            return false;
                                        }
                                    })
                        }
                    })
                });
            },
        //   分享到朋友圈
            toFriendQuan(){
                    var vm=this
                    var realLocation=vm.apiH+'/#/PlayerStyleDetailed?v_id='+vm.currentPlayerData.v_id+'&id='+vm.currentPlayerData.id
                    wx.ready(function () {   //需在用户可能点击分享按钮前就先调用
                        wx.onMenuShareTimeline({
                                title:vm.test.test, // 分享标题
                                link: vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation),  // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                                imgUrl: vm.video_info.username, // 分享图标
                                success: function (res) {
                                      var GetVotesData=qs.stringify({
                                          type:3,
                                          amount:1
                                      })
                                      vm.$http.post('api/user/get_votes',GetVotesData).then((resTwo)=>{
                                          if(res.data.code!==200){
                                              alert('yes')
                                              clearInterval(vm.timer2);
                                              vm.promptContent=resTwo.data.msg
                                              vm.showPrompt=true
                                              vm.timer2=setTimeout(function(){
                                                  vm.showPrompt=false
                                                  clearInterval(vm.timer2);
                                              },2000)
                                              return false;
                                          }
                                      })
                                },
                        })
                });
            },
      //   返回
      toReturn() {
          if(this.playerVideoPage){
              this.$router.push(this.playerVideoPage);
          }else{
              this.$router.push('/');
          }
      },
      playOrPause(){
        
        // 正在播放
        if(this.video.paused) {
          this.video.play();
          this.show = false
          this.showIPhone=false
        } else {
          this.video.pause();
          this.show = true
          this.showIPhone=true
        }

      },
      //   跳选手详情
      toPlayerDetail(playerId){
        this.huaPlayerIds(playerId)
        this.ShowDetailsPages('/PlayerStyleDetailed')
        this.$router.push('/ShowDetails')
      },
      toastMsg(msg) {
        var self = this
        clearInterval(self.timer2);
        this.promptContent = msg
        this.showPrompt = true
        self.timer2 = setTimeout(function () {
          self.showPrompt = false
          clearInterval(self.timer2);
        }, 2000)
      },
      ...mapMutations(['playerIds','PlayerDetailPages','addressIdIsSels','playDetailVoteDivs','PlayerStyleDetailedPlayer','huaPlayerIds','ShowDetailsPages']),

    }
  }

</script>
<style scoped lang="stylus">
.PlayerStyle{
    width:100%;
}
.PlayerRanking_header{
    height:1.23rem;
    width:100%;
    display :flex;
    align-items:center;
    position :fixed;
	  z-index:998;
    padding:0 0.4rem;
    background:#fff;
     >img{
        width:0.32rem;
        height:0.56rem;
        margin-right:0.32rem;
      } 
}
.hideDiv{
  height:1.23rem;
}
.showInfo{
      display:flex;
      flex-direction :column;
      position:absolute;
      top:1.55rem;
      left:0.4rem;
      z-index:999;
      .showInfo_userName{
        width:5.89rem;
        height:1.187rem;
        margin-bottom:0.32rem;
        background:rgba(255, 252, 237, 1);
        border-radius:0.667rem;
        line-height:1.187rem;
        padding:0 0.32rem;
        font-size:0.48rem;
        clor:rgba(0, 0, 0, 1);
      }
      .showInfo_ranking{
        width:2.99rem;
        height:0.48rem;
        line-height:0.48rem;
        font-size:0.32rem;
        color:rgba(0, 0, 0, 0.8);
        background :rgba(255, 252, 237, 1);
         border-radius:0.667rem;
         padding:0 0.32rem;
      }
      
     
}
.player_top{
      display :flex;
      align-items :center;
      justify-content :space-between;
      background:rgba(255,255,255,0.8);
      padding:0.14rem;
      border-radius:0.92rem;
      .player_name{
          display :flex;
          align-items :center;
          >img{
              width:0.853rem;
              height:0.853rem;
              background :pink;
              margin-right:0.187rem;
              border-radius:0.92rem;
          }
          >span{
              font-size:0.44rem;
              color:rgba(0, 0, 0, 0.8);
              font-weight:550;
          }
				>.guanzhu{background:rgba(255,157,172,1); width:1.6rem; height:0.8rem; border-radius:0.92rem; line-height:0.8rem; color:#fff; text-align:center; font-size:0.42rem; margin-left:0.3rem;}
            }
        }
        >.player_center{
            width:4.44rem;
            height:4.44rem;
            margin-bottom:0.347rem;
            >video{
                width:100%;
                height:100%;
            }
			>.bofang{ width:100%; height:2rem; text-align:center; margin-top:-3.4rem;
			>img{ width:1.6rem; height:1.6rem;}
			}
			>img{ width:4.44rem; height:4.44rem;border-radius:0.2rem 0.2rem 0rem 0rem;}
        }
        >.player_bottom{
            display :flex;
            align-items :center;
            justify-content :space-between;
            font-size:0.32rem;
            color:rgba(0, 0, 0, 1);
            >.player_Number{
                font-size:0.32rem;
                color:rgba(0, 0, 0, 0.8);
            }
        }
.video{ width:100%; height:100%; background:#444; position: fixed; top:100;
     >video{ width:100%; height:100%;}
}
.player_paiming{ position:fixed;top:1.8rem;left:0.9rem;z-index:999;  text-align:center; font-size:0.37rem; background:rgba(255,255,255,0.8);  border-radius:0.4rem; padding:0.1rem 0.2rem;}
// 提示盒子
.promptFather{
    width:100%;
    position :fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto;
    display :flex;
    justify-content :center;
    align-items :center;
    z-index:999;
    >.prompt{
        padding:0.2rem 0.35rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.37rem;
    }
}
*::-webkit-media-controls-panel {
  display: none!important;
  -webkit-appearance: none;

}
*::--webkit-media-controls-play-button {
  display: none!important;
  -webkit-appearance: none;
}

*::-webkit-media-controls-start-playback-button {
  display: none!important;
  -webkit-appearance: none;
}
.video_caozuo{ position: fixed; z-index:999; bottom:0.4rem; right:0.27rem; display:flex; align-items:center; justify-content:space-between;
>img{ height:0.6rem; margin-right:0.4rem;}
>.video_toupiao{width:2.09rem;height:0.84rem;line-height:0.84rem;text-align :center; background:rgba(255, 252, 237, 1);   border-radius:0.67rem; font-size:0.373rem;color:rgba(220, 178, 33, 1);}
>.video_fenxiang{ background:rgba(255,255,255,0.8); border-radius:0.92rem; width:1rem; height:1rem; text-align:center; margin-right:0.4rem;
>img{ height:0.6rem; margin-top:0.2rem;}
}
}

.video-js .vjs-big-play-button{
  background-color: red;
}
.ovideo{width:100%; height:100%; background:#444; position: fixed;}
.contenter{width:100%; height:100%; position: fixed;}
.videoBox{width:100%; height:100%; position: fixed;}
.videoBox{
    #output{
        position:absolute;
        top:0;
        left:0;
    }
}
.mask{width:100%; height:100%; position: fixed; z-index:999; text-align:center;
>img{ width:1.6rem; position:fixed; top:48%; left:42%; z-index:9999;}
}
.video-js.vjs-fluid, .video-js.vjs-16-9, .video-js.vjs-4-3{width:100%!important; height:100%!important; background:#444!important; position: fixed!important;}
// 提示分享
.shareText_wrap{
    width:100%;
    height:100%;
    background:rgba(0,0,0,0.9);
    position:fixed;
    top:0;
    left:0;
    z-index:999;
    >img{
        width:2.51rem;
        height:auto;
        position:absolute;
        right:0.32rem;
        top:0.32rem;
    }
    >.shareText{
        width:7.733rem;
        height:5.01rem;
        position:absolute;
        left:0;
        right:0;
        top:0;
        bottom:0;
        margin:auto;
        font-size:0.48rem;
        text-align:center;
        padding:0.4rem;
        line-height:1rem;
        color:#fff;
    }
}
.videoOpcity{
  opacity:0;
}

.scrollWrap{
  width:100%;
  height:0.05rem;
  background:#251c1c;
  position:fixed;
  bottom:1.6rem;
  left:0;
  border-radius:0.5rem;
  >.scrollDiv{
    height:0.05rem;
    background:#d67b8b;
    border-radius:0.5rem;
  }
}
</style>



