<!-- 最新通告 赛事秘籍-->
<template>
  <div class="SpecialDetails">
    
     
      <div class="shopHide"></div>
      <div class="SpecialDetails_title" v-if="specialDetailData">
          {{specialDetailData.title}}
      </div>
      <div class="SpecialDetails_Con" v-if="specialDetailData" v-html="specialDetailData.content"></div>
      <Foot class="PublicFoot" />  <!-- 公共底部 -->
      <div class="footer">
      </div>
  </div>
</template>

<script>
import Foot from './../Foot/Foot'
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import qs from 'qs'
export default {
  data () {
    return {
      specialDetailData:'',
    };
  },
  computed:{
        ...mapState(['staticImgH','RaceCheatsId','RaceCheatsPage'])
    },

  mounted() {
        var obj=qs.stringify({
            id:71
        })
        this.$http.post('/yuanxiao/ad/books',obj).then((res)=>{
            if(res.data.code==200){
                this.specialDetailData=res.data.data
            }
        })
  },
   components: {
      Foot
  },
}

</script>
<style scoped lang="stylus">
.PublicFoot{
    position :fixed;
    bottom:0;
    left:0;
}
.footer{
   width:100%;
   height:1.458rem;
   background :#fff;
}
.SpecialDetails{
    background :#fff;
    img{
        width:100%;
        height:auto;
    }
}
.shopHide{
  width:100%;
  height:1.2rem;
}
.SpecialDetailsHeader{
  width:100%;
  height:1.2rem;
  display:flex;
  justify-content:center;
  align-items :center;
  position :fixed;
  background :#fff;
   >img{
        width:0.32rem;
        height:0.56rem;
        position :absolute;
        top:50%;
        margin-top:-0.28rem;
        left:0.27rem;
    }
  >span{
    font-size:0.53rem;
    color:#000000;
  }
}

.SpecialDetails_title{
    width:100%;
    text-align :center;
    font-size:0.5rem;
    font-weight:600;
    margin-top:0.44rem;
    margin-bottom:0.71rem;
    padding:0 0.27rem;
}
.SpecialDetails_Con{
    width:100%;
  padding:0 0.4rem;
  font-size:0.4rem;
  >>>p{
      >span{
          position :relative!important;
      }
      img{
          width:100%!important;
          display :block;
      }
  }
}
.SpecialDetails_thumb{
  padding:0.2rem 0.4rem;
  >img{ width:100%;}
}
.SpecialDetails_des{
    display :flex;
    align-items :center;
    justify-content :space-between;
    padding:0 0.27rem;
    color:#7a7a7a;
    font-size:0.32rem;
    margin-bottom:0.44rem;
}
.SpecialDetails_foot{
    width:100%;
    height:1.244rem;
    position :fixed;
    bottom:0;
    left:0;
    display:flex;
    >li{
        width:50%;
         height:1.244rem;
         display :flex;
         flex-direction :column;
         align-items :center;
         font-size:0.32rem;
         color:#464543;
         >img{
             margin-bottom:0.16rem;
         }
         
    }
    >.SpecialDetails_collect{
        >img{
            width:0.533rem;
            height:0.516rem;
            background :pink;
        }
    }
    >.SpecialDetails_share{
        >img{
            width:0.516rem;
            height:0.516rem;
            background :pink;
        }
    }
}
.SpecialDetails_But{ padding:0.9rem 0.4rem; text-align:center;}
.SpecialDetails_But button{ border:0; background:#FF9DAC; width:100%; text-align:center; border-radius:100px; height:0.916rem; color:#Fff; font-size:0.40rem;}
</style>


